import React from "react";
import Slider from "react-slick";
import { useNavigate } from "react-router-dom";
import "./FurnitureDeals.css";

const furnitureProducts = [
  {
    id: 1,
    name: "Mattresses",
    price: "2,990",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/mattress/y/p/4/-original-imagw4ugzud7xnyq.jpeg?q=70",
  },
  {
    id: 2,
    name: "Sofa & Sectional",
    price: "7,999",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/sofa-sectional/q/f/y/-original-imagzv7kxgf4ghsh.jpeg?q=70",
  },
  {
    id: 3,
    name: "Office Study Chairs",
    price: "1,890",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/office-study-chair/g/m/m/-original-imaghgchbyr6ffmh.jpeg?q=70",
  },
  {
    id: 4,
    name: "Beds",
    price: "1,790",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/bed/2/g/m/-original-imagzv6jzdpqzckw.jpeg?q=70",
  },
  {
    id: 5,
    name: "TV Units",
    price: "1,249",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/tv-entertainment-unit/n/i/4/-original-imagzv6jzdpqzckw.jpeg?q=70",
  },
  {
    id: 6,
    name: "Sofa Beds",
    price: "6,099",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/sofa-bed/e/v/m/-original-imaghgchbyr6ffmh.jpeg?q=70",
  },
  {
    id: 7,
    name: "Sofa Set",
    price: "21,999",
    image: "https://rukminim2.flixcart.com/image/312/312/xif0q/sofa-sectional/q/f/y/-original-imagzv7kxgf4ghsh.jpeg?q=70",
  },
];

const FurnitureDeals = () => {
  const navigate = useNavigate();

  const settings = {
    dots: false,
    infinite: true,
    speed: 600,
    slidesToShow: 5,
    slidesToScroll: 2,
    arrows: true,
    responsive: [
      { breakpoint: 1024, settings: { slidesToShow: 3 } },
      { breakpoint: 600, settings: { slidesToShow: 2 } },
    ],
  };

  const handleClick = (item) => {
    navigate(`/plp?category=furniture&product=${item.name}`);
  };

  return (
    <div className="furniture-deals">
      <h2>Furniture Deals</h2>
      <Slider {...settings}>
        {furnitureProducts.map((item) => (
          <div
            key={item.id}
            className="furniture-card"
            onClick={() => handleClick(item)}
          >
            <img src={item.image} alt={item.name} />
            <h4>{item.name}</h4>
            <p>From <strong>{item.price}</strong></p>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default FurnitureDeals;
